@import "partials/sections";

$semi-white: lighten($border-color, 20%);

.form {
  min-width: 750px;
  margin: -50px 100px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  background-color: $white;
  border: 1px solid $border-color;
  @include shadow();
  color: lighten(#000000, 30%);

  h1 {
    padding: 0 20px; }

  &>div {
    padding: 0 20px; } }

.form__error {
  color: #b74242;


  &:focus {
    outline: none;
    @include focus(); } }

.form__customer {
  label {
    margin-right: 10px; } }

.form__lot--details, .form__customer {
  display: flex;
  flex-wrap: wrap; }

.form__submit {
  margin-bottom: 25px;
  margin-right: 55px; }

.form__submit--btn {
  margin-top: 10px;
  font-weight: bold;
  width: 120px;
  padding: 15px;
  color: #fff;
  background-color: $border-color;
  border: 1px solid darken($border-color, 10%) {
    radius: 5px; }
  float: right;
  @include transition();
  @include shadow();

  &:hover {
    background-color: darken($semi-white, 10%);
    box-shadow: 0 17px 20px 0 rgba(0, 0, 0, 0.2); }

  &:disabled {
    background-color: darken($semi-white, 10%);
    @include shadow();
    cursor: not-allowed; } }


.form__category {
  display: flex;
  flex-wrap: wrap; }


.multiselect {
  border: 1px solid $border-color !important;
  background-color: $white; }

.select-group {
  display: flex;
  flex-wrap: wrap; }

.form__category--failure__mode,
.form__station,
.form__machine,
.form__customer {
  position: relative;
  margin-right: 10px; }

.multiselect__station,
.multiselect__machine,
.multiselect__customer {
  min-width: 250px; }


.multiselect__failure_mode {
  min-width: 254px * 2; }

.multiselect__employee_list {
    min-width: 254px * 4 + 20; }

.multiselect__discrepancy_category {
  min-width: 254px * 2 + 10; }

.form__textarea {
  resize: none;
  border: 1px solid $border-color;
  background-color: $white;
  @include transition();
  width: 100%;
  min-width: 650px;
  max-width: 254px * 4 + 26; }
